<div class="cc-list-item"><div class="cc-list-item-top" :style="{background: `left 0%/100% 100% no-repeat url(${transformSpace(cardData.path)})`}"></div><div class="cc-list-item-bottom"><div class="cc-list-item-bottom-title-div"><a-tooltip class="cc-list-item-bottom-title" name="title" :title="cardData.title">{{ cardData.title }}</a-tooltip></div><div slot="types"><div class="cc-list-item-bottom-type-tag cc-gray-text dib mr8" v-for="type in cardData.types" :key="type">{{type}}</div></div><slot name="status"><div class="cc-list-item-bottom-status"><span class="dib cc-dot mr8" v-if="!cover" :class="[cardData.publish ? 'cc-publish':'']">{{cardData.publish ? '已发布':'未发布'}}</span><span class="dib cc-gray-text">创建者：{{cardData.creator}}</span><div class="rel dib" style="float:right"><slot name="option" v-if="!cover"><EllipsisOutlined class="dib" type="ellipsis" @click="handleOptionClick"></EllipsisOutlined></slot></div></div></slot></div><div class="cc-list-item-cover" v-if="cover"><slot name="cover"><a-button @click="handleDistributeClick">分配实训</a-button></slot></div></div>